.banner-container {
    position: relative;
    height: 100%;
    overflow: hidden;
}

.banner-text {
    background-color: rgb(0, 0, 0, 50%);
    text-shadow: 5px 5px rgb(134, 130, 130);
    opacity: 0;
    padding: 20px 20px;
    font-size: 7vw;
    color: blanchedalmond;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -webkit-animation: showUp 1.5s 1s;
            animation: showUp 1.5s 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    z-index: 10;
}

@-webkit-keyframes showUp {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes showUp {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@-webkit-keyframes enlarge {
    from {
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1)
    }
    to {
        -webkit-transform: scale(1.15, 1.15);
                transform: scale(1.15, 1.15);
    }
}

@keyframes enlarge {
    from {
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1)
    }
    to {
        -webkit-transform: scale(1.15, 1.15);
                transform: scale(1.15, 1.15);
    }
}

.banner-image {
    -webkit-animation: enlarge 6s 2s forwards;
            animation: enlarge 6s 2s forwards;
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
    width: 100%;
    max-height: 100vh;
    object-fit: cover;
}

.section-picture {
    width: 50%;
    min-height: 100%;
    object-fit: cover;
    position: absolute;
    z-index: 10;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    transition: -webkit-transform 1.5s;
    transition: transform 1.5s;
    transition: transform 1.5s, -webkit-transform 1.5s;
}

.section-container:hover>.section-picture {
    -webkit-transform: translate(100%, 0);
            transform: translate(100%, 0);
    transition: -webkit-transform 1.5s;
    transition: transform 1.5s;
    transition: transform 1.5s, -webkit-transform 1.5s;
    z-index: 10;
}

.section-container {
    border: white 2px solid;
    height: 35vh;
    background-color: #2E544A;
    position: relative;
    overflow: hidden;
}

.section-hidden-text,
.section-text {
    color: white;
    font-size: 5vh;
    position: absolute;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 4;
}

.section-hidden-text {
    width: 250px;
    overflow: visible;
    top: 50%;
    left: 30%;
}

.section-text {
    top: 50%;
    left: 80%;
}
.product-image {
    width: 300px;
    height: 200px;
    object-fit: cover;
}

.product-container {
    border: 2px rgb(156, 156, 156) solid;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.product-container:nth-child(n+1) {
    margin-left: -1px;
}

.product-container .product-price {
    padding: 0px 3px;
    color: rgb(230, 159, 101)
}

.products-search {
    width: 40%;
    border: 3px solid #2E544A;
    padding: 5px;
    height: 30px;
    border-radius: 5px 0 0 5px;
    outline: none;
    color: #000000;
    margin: 1% 1%;
}

.products-search-container {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
}
.cartItem-image {
    width: 150px;
    height: 100px;
    object-fit: cover;
}
.login-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 100vh;
}
